export const getPeriodById = (
  allSession: SessionPlanInterface[],
  period_id: string
): SessionPlanInterface | undefined => {
  return allSession.find((item) => item._id === period_id);
};

const searchData = (
  objList: WaitingListDataInterface[],
  text: string
): WaitingListDataInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "names" ||
        prop === "gender" ||
        prop === "national_id" ||
        prop === "phone" ||
        prop === "email" ||
        prop === "user_position_marks" ||
        prop === "position_title" ||
        prop === "education_category" ||
        prop === "waitingList_session" ||
        prop === "date" ||
        prop === "waiting_status"
      ) {
        if (product[prop]) {
          flag =
            product[prop]!.toString()
              .toLowerCase()
              .indexOf(text.toLowerCase()) > -1;
        }
      }
      if (flag) break;
    }
    return flag;
  });
};

